<template>
  <div class="login">
    <v-parallax :src="b64" height="800px">
      <v-flex flex xs10 sm10 md4 offset-md4 offset-sm1 offset-xs1>
        <form>
          <v-text-field
            outline
            v-model="userName"
            :rules="[nameRules.required, nameRules.min]"
            type="text"
            maxlength="16"
            name="input-10-1"
            label="用户名"
            hint
            counter
          ></v-text-field>
          <v-text-field
            outline
            v-model="password"
            :append-icon="show2 ? 'visibility' : 'visibility_off'"
            :rules="[passwordRules.required, passwordRules.min]"
            :type="show2 ? 'text' : 'password'"
            name="input-10-2"
            maxlength="26"
            label="登录密码"
            hint
            counter
            @click:append="show2 = !show2"
          ></v-text-field>
          <v-checkbox
            v-model="checkbox_"
            color="red darken-3"
            :label="`记住密码? `"
          ></v-checkbox>
          <!-- <v-switch
            v-model="checkbox_"
            color="orange darken-3"
            hide-details
            :label="`记住密码?`"
          ></v-switch> -->
          <!-- <br /> -->
          <v-btn :loading="loader" :disabled="loader" @click="submit_" color="info" block large>登录</v-btn>
          <!-- <p> {{  "★★★★★☆☆☆☆☆".slice(5 - rate, 10 - rate) }} </p> -->
          <!-- <v-btn @click="aaa">存LO</v-btn>
          <v-btn @click="bbb">取LO</v-btn>-->
          <!-- <v-btn @click="klkl">删LO</v-btn> -->
        </form>
      </v-flex>
    </v-parallax>

    <!-- <v-upload
          v-model="brand.image"
          url="/item/upload"
          :multiple="false"
          :pic-width="150"
          :pic-height="150"
    />-->

    <v-snackbar
      v-model="snackbar"
      :color="`error`"
      :multi-line="true"
      :timeout="3000"
      :vertical="false"
    >
      {{ text }}
      <v-btn dark flat @click="snackbar = false">关闭</v-btn>
    </v-snackbar>
  </div>
</template>

<script>
import Http from "../../server/http.js";
import Cookies from "../../server/cookies.js";
// import { setTimeout } from 'timers';
import Api from "../../server/api.js";
// 随机背景图
const b64 = [
              "data:image/svg+xml;base64,",
              "data:image/svg+xml;base64,",
              "data:image/svg+xml;base64,",
              "data:image/svg+xml;base64,",
              "data:image/svg+xml;base64,",
              "data:image/svg+xml;base64,",
              "data:image/svg+xml;base64,",
              "data:image/svg+xml;base64,",
              "data:image/svg+xml;base64,",
              "data:image/svg+xml;base64,"
            ];
export default {
  components: {},
  data() {
    return {
      b64: b64[(~~(Math.random()*10+1)-1)],
      brand: {
        image: ""
      },
      loader: false,
      userName: "",
      password: "",
      checkbox_: false,
      show1: false,
      show2: false,
      nameRules: {
        required: value => !!value || "用户名为5-16位",
        min: v => v.length >= 5 || "至少5位字符",
        emailMatch: () => "The email and password you entered don't match"
      },
      passwordRules: {
        required: value => !!value || "用户密码为6-26位",
        min: v => v.length >= 6 || "至少6位字符",
        emailMatch: () => "The email and password you entered don't match"
      },
      text: "",
      snackbar: false,
    };
  },
  // 计算属性
  computed: {},
  // 更新完成
  mounted: function() {
    this.getBrowserInfo();
    this.remember();
    // window.devicePixelRatio
  },
  methods: {
    // 下载
    download(){
      alert.snackbar = false;
      window.open("https://www.google.cn/chrome/");
    },
    // 提交
    submit_() {
      if (this.userName.length < 5) {
        return false;
      }
      if (this.password.length < 6) {
        return false;
      }
      this.loader = true;
      this.$axios({
        method: "post",
        url: Api.URL.manages_login,
        // this.qs.stringify({})
        data: {
          user_name: this.userName,
          password: this.password
        }
      })
        .then(response => {
          this.loader = false;
          //请求成功返回的数据
          if (response.data.result === 0) {
            if (this.checkbox_) {
              Cookies.setCookie(
                "user",
                JSON.stringify({
                  user: this.userName,
                  password: this.password
                }),
                7*2
              );
            } else {
              this.del();
            }
            // 保存 - 权限信息
            Cookies.setLocalStorage(
              "root_path",
              JSON.stringify(response.data.datas)
            );
            if (response.data.datas.status == 1) {
              this.$router.push({
                path: `/home`
              });
            } else if (response.data.datas.status == -1) {
              this.$router.push({
                path: `/modifyPass`
              });
            }
          } else {
            this.snackbar = true;
            if (response.data.result == 1014) {
              this.text = `${response.data.message} (如果忘记密码，请联系 Hiensor 进行修改!)`;
            } else this.text = response.data.message;
          }
        })
        .catch(error => {
          this.snackbar = true;
          this.text = error;
        });
    },
    // 记住密码
    remember() {
      if (Cookies.getCookie("user") == null) {
        this.checkbox_ = false;
        return false;
      } else {
        this.checkbox_ = true;
        let user = Cookies.getCookie("user");
        this.userName = user.user;
        this.password = user.password;
      }
    },
    // 置空用户输入
    clear() {
      this.userName = "";
      this.password = "";
      this.checkbox_ = false;
    },
    // 删除cookies密码
    del() {
      Cookies.delCookie("user");
    },
    // 判断浏览器类型
    getBrowserInfo() {
      var ua = navigator.userAgent.toLocaleLowerCase();
      var browserType = null;
      if (ua.match(/msie/) != null || ua.match(/trident/) != null) {
        browserType = "IE浏览器";
        browserVersion = ua.match(/msie ([\d.]+)/) != null ? ua.match(/msie ([\d.]+)/)[1] : ua.match(/rv:([\d.]+)/)[1];
      } else if (ua.match(/firefox/) != null) {
        browserType = "火狐浏览器";
      } else if (ua.match(/edge/) != null) {
        browserType = "IE浏览器";
      } else if (ua.match(/ubrowser/) != null) {
        browserType = "UC浏览器";
      } else if (ua.match(/opera/) != null) {
        browserType = "欧朋浏览器";
      } else if (ua.match(/maxthon/) != null) {
        browserType = "遨游浏览器";
      } else if (ua.match(/chrome/) != null) {
        var is360 = _mime("type", "application/vnd.chromium.remoting-viewer");
        function _mime(option, value) {
          var mimeTypes = navigator.mimeTypes;
          for (var mt in mimeTypes) {
            if (mimeTypes[mt][option] == value) {
              return true;
            }
          }
          return false;
        }
        if (is360) {
          browserType = "360浏览器";
        } else {
          document.createElement("html").style.zoom = ".80";
        }
      } else if (ua.match(/safari/) != null) {
        browserType = "Safari浏览器";
      }
      if (browserType !== null) {
        this.$Dialog.getProductCheckResult_({text: ` ${browserType} ： 为了更好的用户体验，请使用 Chrome 浏览器`,timeout: 3000, snackbar: true });
      }
    }
  }
};
</script>

<style lang="less" scoped>
.login {
  height: 100%;
  width: 100%;
}
.v-parallax {
  height: 100vh !important;
}
form {
  margin: 0 auto;
  background-color: rgba(255, 255, 255, 1);
  padding: 50px 50px 40px 50px;
  border-radius: 2px;
  will-change: box-shadow;
    box-shadow: 0px 3px 1px -2px rgba(0,0,0,0.2), 0px 2px 2px 0px rgba(0,0,0,0.14), 0px 1px 5px 0px rgba(0,0,0,0.12);
}
</style>
<style>
.login .v-input__slot{
  margin-bottom: 8px !important;
}
</style>