import Vue from 'vue';
import './plugins/vuetify';
import App from './App.vue';
import router from './router';
import axios from './axios';
import * as filters from './server/filter.js';
import Cookie from './server/cookies.js';
import Apis from "./server/api.js";

// 提示框组件
import getProductCheckResult from "./views/promptBox/promptBox.js";
Vue.use(getProductCheckResult);

// 引入 echarts 图表组件库
// import echarts from 'echarts';

// eslint
Vue.config.productionTip = false;

/*
全局注册
使用方法为:this.$axios / this.$cookie / this.$apis
 */
Vue.prototype.$apis = Apis;
Vue.prototype.$cookie = Cookie;
// Vue.prototype.$echarts = echarts;

// 全局全部过滤器
Object.keys(filters).forEach(key => {
    Vue.filter(key, filters[key]);
});

// 如果未匹配到路由跳转到上级否则跳转到登录
router.beforeEach((to, from, next) => {
    if (to.matched.length === 0) { from.name ? next({ name: from.name }) : next('/'); } else { next(); }
});

new Vue({
    router,
    axios,
    render: h => h(App)
}).$mount("#app")